@import url(https://fonts.googleapis.com/css?family=Orbitron&display=swap);
* {
  box-sizing: border-box;
  font-family: 'Orbitron', sans-serif;
}

body {
  margin: 0;
}

.screen {
  background-color: rgb(26, 117, 255);
  padding: 10px;
  font-size: 20px;
  border-radius: 5px;
  color: #303030;
}

.screen-text {
  margin: 0;
}
.button {
  color: black;
  background-color: rgb(255, 255, 102);
  border: 1px solid black;
  padding: auto;
  font-size: 30px;
  height: 50px;
  width: 50px;
  border-radius: 5px;
}

.button:hover {
  cursor: pointer;
}

#clear {
  font-size: 20px;
  background-color: rgb(255, 26, 26);
}

#equals {
  background-color: rgb(51, 204, 51);
}
.container {
  width: 320px;
  box-shadow: 0 2px 10px gray;
  border-radius: 20px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 100px;
  padding: 20px;
  background-color: rgb(0, 41, 102);
}

.button-box {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-around;
  margin-top: 20px;
}
